/* Project Module Theme Variables */
:root {
  /* Light theme - Orange colors */
  --project-bg-primary: #ffffff;
  --project-bg-secondary: #fff8f5;
  --project-bg-tertiary: #ffe8dc;
  --project-text-primary: #212529;
  --project-text-secondary: #6c757d;
  --project-text-tertiary: #adb5bd;
  --project-border-color: #dee2e6;
  --project-shadow: rgba(0, 0, 0, 0.1);
  
  /* Primary colors - Orange theme */
  --project-primary: #ff6b35;
  --project-primary-dark: #e55a2b;
  --project-primary-light: #ff8746;
  --project-accent: #ff6b35;
  --project-accent-secondary: #e55a2b;
  
  /* Status colors */
  --project-success: #198754;
  --project-warning: #ffc107;
  --project-error: #dc3545;
  --project-info: #0dcaf0;
  
  /* Gradients - Orange */
  --project-gradient-primary: linear-gradient(135deg, #ff6b35 0%, #e55a2b 100%);
  --project-gradient-secondary: linear-gradient(135deg, #fff8f5 0%, #ffe8dc 100%);
  --project-gradient-accent: linear-gradient(135deg, #ff8746 0%, #ff6b35 100%);
  --project-gradient-card: linear-gradient(135deg, rgba(255, 107, 53, 0.05) 0%, rgba(229, 90, 43, 0.05) 100%);
}

/* Dark theme - Green colors */
.dark-theme {
  --project-bg-primary: #1a1a1a;
  --project-bg-secondary: #1f2d1f;
  --project-bg-tertiary: #2a4029;
  --project-text-primary: #ffffff;
  --project-text-secondary: #b0b0b0;
  --project-text-tertiary: #8a8a8a;
  --project-border-color: #2a4029;
  --project-shadow: rgba(0, 0, 0, 0.3);
  
  /* Primary colors - Green theme */
  --project-primary: #00d4aa;
  --project-primary-dark: #00b894;
  --project-primary-light: #00e5bf;
  --project-accent: #00d4aa;
  --project-accent-secondary: #00b894;
  
  /* Status colors */
  --project-success: #4caf50;
  --project-warning: #ff9800;
  --project-error: #f44336;
  --project-info: #2196f3;
  
  /* Gradients - Green */
  --project-gradient-primary: linear-gradient(135deg, #00d4aa 0%, #00b894 100%);
  --project-gradient-secondary: linear-gradient(135deg, #1f2d1f 0%, #2a4029 100%);
  --project-gradient-accent: linear-gradient(135deg, #00e5bf 0%, #00d4aa 100%);
  --project-gradient-card: linear-gradient(135deg, rgba(0, 212, 170, 0.05) 0%, rgba(0, 184, 148, 0.05) 100%);
}

/* Light theme explicit - Orange */
.light-theme {
  --project-bg-primary: #ffffff;
  --project-bg-secondary: #fff8f5;
  --project-bg-tertiary: #ffe8dc;
  --project-text-primary: #212529;
  --project-text-secondary: #6c757d;
  --project-text-tertiary: #adb5bd;
  --project-border-color: #dee2e6;
  --project-shadow: rgba(0, 0, 0, 0.1);
  --project-accent: #ff6b35;
  --project-accent-secondary: #e55a2b;
  
  /* Gradients */
  --project-gradient-primary: linear-gradient(135deg, #ff6b35 0%, #e55a2b 100%);
  --project-gradient-secondary: linear-gradient(135deg, #fff8f5 0%, #ffe8dc 100%);
  --project-gradient-accent: linear-gradient(135deg, #ff8746 0%, #ff6b35 100%);
  --project-gradient-card: linear-gradient(135deg, rgba(255, 107, 53, 0.05) 0%, rgba(229, 90, 43, 0.05) 100%);
}

/* Theme transition */
.project-module * {
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
}

/* Custom scrollbar */
.project-module ::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.project-module ::-webkit-scrollbar-track {
  background: var(--project-bg-secondary);
  border-radius: 8px;
}

.project-module ::-webkit-scrollbar-thumb {
  background: var(--project-gradient-primary);
  border-radius: 8px;
  
  &:hover {
    background: var(--project-primary-dark);
  }
}

/* Dark theme scrollbar */
.dark-theme .project-module ::-webkit-scrollbar-track {
  background: var(--project-bg-tertiary);
}

.dark-theme .project-module ::-webkit-scrollbar-thumb {
  background: var(--project-gradient-primary);
  
  &:hover {
    background: var(--project-primary-light);
  }
}

/* Material component overrides */
.project-module {
  /* Cards */
  .mat-mdc-card {
    background: var(--project-bg-primary);
    color: var(--project-text-primary);
    border: 1px solid var(--project-border-color);
  }

  /* Buttons */
  .mat-mdc-button,
  .mat-mdc-raised-button,
  .mat-mdc-fab,
  .mat-mdc-mini-fab {
    &.mat-primary {
      background: var(--project-gradient-primary) !important;
      color: white !important;
      
      &:hover {
        box-shadow: 0 4px 12px rgba(255, 107, 53, 0.3);
        transform: translateY(-2px);
      }
    }
  }

  /* Toolbar */
  .mat-toolbar {
    background: var(--project-gradient-primary);
    color: white;
  }

  /* Input fields */
  .mat-mdc-form-field {
    .mat-mdc-text-field-wrapper {
      background: var(--project-bg-secondary);
    }
    
    .mat-mdc-form-field-focus-overlay {
      background-color: var(--project-accent);
    }
  }

  /* Chips */
  .mat-mdc-chip {
    background: var(--project-bg-tertiary);
    color: var(--project-text-primary);
    border: 1px solid var(--project-border-color);
    
    &.mat-primary {
      background: var(--project-gradient-primary);
      color: white;
    }
  }

  /* Dialogs */
  .mat-mdc-dialog-container {
    background: var(--project-bg-primary);
    color: var(--project-text-primary);
  }

  /* Sidenav */
  .mat-drawer {
    background: var(--project-bg-primary);
    border-right: 1px solid var(--project-border-color);
  }

  .mat-drawer-content {
    background: var(--project-bg-secondary);
  }
}

/* Animation keyframes */
@keyframes project-shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}

@keyframes project-pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

@keyframes project-bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
