@use '../../styles/theme.scss';

:host {
  display: block;
  background: var(--project-bg-secondary);
  min-height: 100%;
}

.project-list-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 24px;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    padding-bottom: 16px;
    border-bottom: 2px solid var(--project-border-color);

    h1 {
      margin: 0;
      font-size: 28px;
      font-weight: 700;
      color: var(--project-text-primary);
      background: var(--project-gradient-primary);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
    }

    button {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 12px 24px;
      background: var(--project-gradient-primary);
      color: white;
      border: none;
      border-radius: 12px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 24px var(--project-shadow);
      }
    }
  }

  .project-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
    gap: 24px;

    .project-card {
      background: var(--project-bg-primary);
      border: 1px solid var(--project-border-color);
      border-radius: 16px;
      overflow: hidden;
      cursor: pointer;
      transition: all 0.3s ease;

      &:hover {
        transform: translateY(-6px);
        box-shadow: 0 12px 32px var(--project-shadow);
      }
    }
  }
}
